<template>
  <div id="app2">
    <div class="menuList">
      <ul>
        <li v-for="(item,index) in list" :key="item.id" :class="{active:num==index}" @click="getNum(index)">
          {{item}}
        </li>
      </ul>
    </div>
    <div class="tabCon">
      <div v-for='(itemCon,index) in tabContents' v-show="index == num">
        {{itemCon}}
      </div>
    </div>
  </div>
</template>

<script>
    export default {
      name: "tabs3",
      data() {
        return {
          num: 0,
          list: ["", "", ""],
          tabContents: [
            "  [1] (1247) 14181280",
            "",
            "-"],
        }
      },
      methods: {
        getNum(index) {
          this.num = index;
        }
      }

    }
</script>

<style scoped>
  html,
  body {
    margin: 0;
    padding: 0;
    background-color: #58596b;
  }
  .active {
    color: #fff;
    background: #e74c3c;
  }
  #app2 {
    width: 70%;
    height: 300px;
    background-color: #fff;
    box-shadow: 0 1px 3px rgba(0, 0, 0, .1);
    /*margin-top:200px;*/
  }
  .menuList {
    width: 100%;
    height: 60px;
    background-color: #33344a;
  }
  ul {
    width: 100%;
    display: flex;
    list-style: none;
    padding: 0;
    margin: 0;
    color: #717181;
    font-size: 16px;
    line-height: 60px;
  }
  ul li {
    flex: 1;
    text-align: center;
    cursor: pointer;
  }
  .tabCon {
    width: 90%;
    margin: 0 auto;
    padding: 40px 20px;
    color: #999;
    font-size: 14px;
    background-color: #fff;
  }
</style>



