<template>
  <div>
    <div id="box">
      <ul id="con1" ref="con1" :class="{anim:animate==true}">
        <li v-for='item in items'>{{item.name}}</li>
      </ul>
    </div>
  </div>
</template>

<script>
    export default {
        name: "notice1",
      data() {
        return {
          animate:false,
          items:[  //
            {name:"1"},
            {name:"2"},
            {name:"3"}
          ]
        }
      },
      created(){
        setInterval(this.scroll,1500) // method scroll()this,this
      },
      methods: {
        scroll(){
          let con1 = this.$refs.con1;
          con1.style.marginTop='-30px';
          this.animate=!this.animate;
          var that = this; // thisthis
          setTimeout(function(){
            that.items.push(that.items[0]);
            that.items.shift();
            con1.style.marginTop='0px';
            that.animate=!that.animate;  // animate ul 
          },500)
        }
      },
    }
</script>

<style scoped>
  *{
    margin: 0 ;
    padding: 0;
  }
  #box{
    width: 300px;
    height: 32px;
    line-height: 30px;
    overflow: hidden;
    padding-left: 30px;
    border: 2px solid darkseagreen;
    transition: all 0.5s;
    border-radius: 7px;
  }
  .anim{
    transition: all 0.5s;
  }
  #con1 li{
    list-style: none;
    line-height: 30px;
    height: 30px;
    font-weight: bold;
  }
</style>



