<template>
 <div id="AppScore" class="row">
      <div class="form-group clearfix">
          <!-- <label class="control-label col-md-4">:{{starNum}}</label> -->
          <div class="col-md-8">
              <div class="grade-box">
                  <img v-for="(star,index) in stars" :src="star.src" @click="rating(index)" alt="" :key='index'/>
              </div>
          </div>
      </div>
  </div>
</template>

<script>
export default {
    name: 'ScoreOne',  
    data(){
        return {
            stars: [
                {
                    src: "../static/img/off.png",
                    active: false
                }, 
                {
                    src: "../static/img/off.png",
                    active: false
                }, 
                {
                    src: "../static/img/off.png",
                    active: false
                },
                {
                    src: "../static/img/off.png",
                    active: false
                }, 
                {
                    src: "../static/img/off.png",
                    active: false
                }
            ],
            starNum: 0,
        }    
    },
    methods: {
        //
        rating: function(index) {
            var total = this.stars.length; //
            var idx = index + 1; //idx-
            //if
            if(this.starNum == 0) {
                this.starNum = idx;
                for(var i = 0; i < idx; i++) {
                    this.stars[i].src = "../static/img/on.png";
                    this.stars[i].active = true;
                }
            } else {
                //-
                if(idx == this.starNum) {
                    for(var i = index; i < total; i++) {
                        this.stars[i].src = "../static/img/off.png";
                        this.stars[i].active = false;
                    }
                }
                //
                if(idx < this.starNum) {
                    for(var i = idx; i < this.starNum; i++) {
                        this.stars[i].src = "../static/img/off.png";
                        this.stars[i].active = false;
                    }
                }
                //
                if(idx > this.starNum) {
                    for(var i = 0; i < idx; i++) {
                        this.stars[i].src = "../static/img/on.png";
                        this.stars[i].active = true;
                    }
                }
                var count = 0; //-
                for(var i = 0; i < total; i++) {
                    if(this.stars[i].active) {
                        count++;
                    }
                }
                this.starNum = count;
            }
        },
    }     
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.grade-box img{
    width: 50px;
    height: 50px;
}
.grade-box{
    margin: 20px;
}
.col-md-8{
    width:100%;
}
.form-group{
    width:100%;
}
.grade-box {
    width:100%;
}

</style>


