<style lang="scss" scoped>
  .Foot {
    width: 100%;
    height: 450px;
    background: #eee;
    padding-top: 100px;
    display: flex;
    box-sizing: border-box;
    box-shadow: 0px 0px 10px 5px  #eee;
    .K-Card {
      padding: 50px;
      margin: 30px;
      font-size: 14px;
      color: #2F4056;
      text-align: center;
      border: 1px solid #666;
       border-radius: 15px;
      box-shadow: 0 0 10px 0 #2F4056;
      h4 {
        font-size: 20px;
        margin-bottom: 30px;
      }

      p {
        display: inline;
        line-height:24px;
      }
     }
  }

</style>



