<template>
    <div class="BigBox">
        <h2>.</h2>
        <p class="state">jQuerylayui.all.jsjslayui.css,
        </p>
        <fieldset class="layui-elem-field layui-field-title" style="margin-top: 30px;">
            <legend></legend>
        </fieldset>

        <div id="demo0"></div>

        <fieldset class="layui-elem-field layui-field-title" style="margin-top: 30px;">
            <legend></legend>
        </fieldset>

        <div id="demo1"></div>

        <fieldset class="layui-elem-field layui-field-title" style="margin-top: 30px;">
            <legend> - </legend>
        </fieldset>

        <div id="demo2"></div>
        <div id="demo2-1"></div>
        <div id="demo2-2"></div>

        <fieldset class="layui-elem-field layui-field-title" style="margin-top: 30px;">
            <legend></legend>
        </fieldset>

        <div id="demo3"></div>

        <fieldset class="layui-elem-field layui-field-title" style="margin-top: 30px;">
            <legend></legend>
        </fieldset>

        <div id="demo4"></div>

        <fieldset class="layui-elem-field layui-field-title" style="margin-top: 30px;">
            <legend>HASH</legend>
        </fieldset>

        <div id="demo5"></div>

        <fieldset class="layui-elem-field layui-field-title" style="margin-top: 30px;">
            <legend></legend>
        </fieldset>

        <div id="demo6"></div>

        <fieldset class="layui-elem-field layui-field-title" style="margin-top: 30px;">
            <legend></legend>
        </fieldset>

        <div id="demo7"></div>

        <fieldset class="layui-elem-field layui-field-title" style="margin-top: 30px;">
            <legend></legend>
        </fieldset>

        <div id="demo8"></div>
        <div id="demo9"></div>
        <div id="demo10"></div>

        <fieldset class="layui-elem-field layui-field-title" style="margin-top: 30px;">
            <legend></legend>
        </fieldset>

        <div id="demo11"></div>



        <fieldset class="layui-elem-field layui-field-title" style="margin-top: 30px;">
            <legend></legend>
        </fieldset>

        <div id="demo20"></div>
        <ul id="biuuu_city_list"></ul>
        <!---->
        <h2 style="margin-top: 15px">.</h2>
        <hr/>
        <div class="code">
            <P id="code_p"></P>
            <p id="code_b"></p>
            <p id="code_c">
                <img src="../../assets/imgs/6.png" alt="">
                <img src="../../assets/imgs/7.png" alt="">
                <img src="../../assets/imgs/8.png" alt="">
                <img src="../../assets/imgs/9.png" alt="">
                <img src="../../assets/imgs/10.png" alt="">
                <img src="../../assets/imgs/11.png" alt="">
                <img src="../../assets/imgs/12.png" alt="">
                <img src="../../assets/imgs/13.png" alt="">
                <img src="../../assets/imgs/14.png" alt="">
            </p>
        </div>
    </div>
</template>

<script>
    import '../../assets/js/layui.all'
    export default {
        name: "WebPaging",
        data(){
           return{
               flag:false
           }
        },
        mounted(){
            // 
            $("#code_p").click(function(){
                $("#code_c").css({"display":"block"})
            });
            $("#code_b").click(function(){
                $("#code_c").css({"display":"none"})
            });
            //js
            layui.use(['laypage', 'layer'], function(){
                var laypage = layui.laypage
                    ,layer = layui.layer;

                //
                laypage.render({
                    elem: 'demo0'
                    ,count: 50 //
                });

                //
                laypage.render({
                    elem: 'demo1'
                    ,count: 70 //
                    ,jump: function(obj){
                        console.log(obj)
                    }
                });

                //
                laypage.render({
                    elem: 'demo2'
                    ,count: 100
                    ,theme: '#1E9FFF'
                });
                laypage.render({
                    elem: 'demo2-1'
                    ,count: 100
                    ,theme: '#FF5722'
                });
                laypage.render({
                    elem: 'demo2-2'
                    ,count: 100
                    ,theme: '#FFB800'
                });

                //
                laypage.render({
                    elem: 'demo3'
                    ,count: 100
                    ,first: ''
                    ,last: ''
                    ,prev: '<em></em>'
                    ,next: '<em></em>'
                });

                //
                laypage.render({
                    elem: 'demo4'
                    ,count: 100
                    ,first: false
                    ,last: false
                });

                //HASH
                laypage.render({
                    elem: 'demo5'
                    ,count: 500
                    ,curr: location.hash.replace('#!fenye=', '') //hashfenye
                    ,hash: 'fenye' //hash
                });

                //
                laypage.render({
                    elem: 'demo6'
                    ,count: 50
                    ,layout: ['prev', 'next']
                    ,jump: function(obj, first){
                        if(!first){
                            layer.msg(' '+ obj.curr +' ');
                        }
                    }
                });

                //
                laypage.render({
                    elem: 'demo7'
                    ,count: 100
                    ,layout: ['count', 'prev', 'page', 'next', 'limit', 'refresh', 'skip']
                    ,jump: function(obj){
                        console.log(obj)
                    }
                });

                //
                laypage.render({
                    elem: 'demo8'
                    ,count: 1000
                    ,layout: ['limit', 'prev', 'page', 'next']
                });
                laypage.render({
                    elem: 'demo9'
                    ,count: 1000
                    ,layout: ['prev', 'next', 'page']
                });
                laypage.render({
                    elem: 'demo10'
                    ,count: 1000
                    ,layout: ['page', 'count']
                });

                //
                laypage.render({
                    elem: 'demo11'
                    ,count: 1000
                    ,limit: 100
                    ,limits: [100, 300, 500]
                });


                //

                //
                var data = ['', '', '', '', '', '', '', '', '',
                    '', '', '', '', '', '', '', '', '', '',
                    '', '', '', '', '',
                    '', '', '', '', '', '', '', '', '',
                    '', '', '', ''
                ];

                //
                laypage.render({
                    elem: 'demo20'
                    ,count: data.length
                    ,jump: function(obj){
                        //
                        document.getElementById('biuuu_city_list').innerHTML = function(){
                            var arr = []
                                ,thisData = data.concat().splice(obj.curr*obj.limit - obj.limit, obj.limit);
                            layui.each(thisData, function(index, item){
                                arr.push('<li>'+ item +'</li>');
                            });
                            return arr.join('');
                        }();
                    }
                });

            });
        }
    }
</script>

<style scoped lang="scss">
    @import "../../assets/css/layui.css";
    .BigBox{
        width: 85%;
        margin-top: 60px;
        float: left;
        margin-left:230px;
    }
    /**/
    .state{
        margin-top: 15px;
        padding-left: 20px;
        width: 88%;
        min-height: 60px;
        line-height: 60px;
        font-size: 18px;
        background:#f2f2f2;
        border-left:4px solid #009688;
    }
    /**/
    .code{
        margin-top: 20px;
        p:nth-of-type(1){
           width: 80px;
            height: 30px;
            background: #009688;
            border-radius: 3px;
            box-shadow: 0 0 20px #009688;
            text-align: center;
            line-height: 30px;
            color: #fff;
            display: inline-block;
            margin-right: 20px;
        }
        p:nth-of-type(2){
            display: inline-block;
            width: 80px;
            height: 30px;
            background: #009688;
            border-radius: 5px;
            box-shadow: 0 0 20px #009688;
            text-align: center;
            line-height: 30px;
            color: #fff;
        }
        p:nth-of-type(3){
            display: none;
            margin-top: 20px;
            img{
                width: 90%;
            }
        }
    }

</style>



