// index.js
import Vue from 'vue';
import Vuex from 'vuex';

// vuexvue
Vue.use(Vuex);

let count = parseInt(localStorage.getItem('count') || '[]');

//  es6 vuex
export default new Vuex.Store({
  state: {
    count: count
  },
  getters: {
    // state count  
    consoleCount: state => {
      console.log('the state count : ' + state.count);
    }
  },
  mutations: {
    addCount: state => {
      ++state.count;

    },
    addNumCount: (state, n) => {
      state.count+=n;
      //localstorage
      localStorage.setItem('count',parseInt(state.count))
    },
    //
    subCount: state=>{
      --state.count;
      localStorage.setItem('count',parseInt(state.count))
    },

  },
  // actions  mutations    this.$store.dispatch 
  actions: {
    addCount: context => {
      //  mutation
      context.commit('addCount');
    },
    addNumCount: (context, n) => {
      context.commit('addNumCount', n);
    },
    subCount: context=>{
      context.commit('subCount')
    }
  }
})



