<template>
    <div class="left">
        <div class="BigBox">
            <h2 style="font-weight: 100;"></h2>
            <hr/>
            <form>
                <label></label><input
                    @blur="RegularUser()"
                    class="FormUser"
                    type="text"
                    placeholder="">
                <i id="UserPrompt"></i><br/>
                <label></label><input
                    @blur="RegularPassWord()"
                    class="FormPassword"
                    type="password"
                    placeholder="">
                <i id="PassWordPrompt"></i><br/>
                <label></label><input
                    @blur="RegularPhone()"
                    class="FromPhone"
                    type="text"
                    placeholder="">
                <i id="PhonePrompt"> </i><br/>
                <label></label><input
                    @blur="RegularEmail()"
                    class="FromEmail"
                    type="text"
                    placeholder="">
                <i id="EmailPrompt"></i><br/>
                <label></label><input
                    @blur="RegularCard()"
                    class="FromCard"
                    type="text"
                    placeholder="">
                <i id="CardPrompt"></i><br/>
                <!---->
                <label class="live"></label>
                <div class="box_01" id="box" @click="flag=!flag">
                    <p id="con"></p>
                    <img id="ps01" src="../../assets/imgs/3.png" alt="">
                    <ul id="uls" v-show="flag">
                        <li></li>
                        <li></li>
                        <li></li>
                        <li></li>
                        <li></li>
                        <li></li>
                        <li></li>
                    </ul>
                </div>
                <br/>
                <!---->
                <div class="check">
                    <label>:</label>
                    <label></label><input type="checkbox">
                    <label></label><input type="checkbox">
                    <label></label><input type="checkbox">
                </div>
                <div class="radio">
                    <label>:</label>
                    <label></label><input type="radio" name="sex">
                    <label></label><input type="radio" name="sex">
                </div>
                <!--  -->
                <div class="address">
                    <label>:</label>
                    <select v-model="user.selCity.sheng">
                    <option :value="s" v-for="s in cityData">{{s.province}}</option>
                </select>
                    <select v-model="user.selCity.shi">
                    <option :value="c" v-for="c in user.selCity.sheng.citys">{{c.city}}</option>
                </select>
                    <select v-model="user.selCity.qu">
                    <option :value="q" v-for="q in user.selCity.shi.areas">{{q}}</option>
                </select>
                </div>
                <!---->

                <div class="text">
                    <label></label><br/>
                    <textarea cols="100" rows="8"></textarea>
                </div>
                <!---->
                <input calss="FormSubmit" type="submit" value="" id="FormSubmit">
                <input calss="FormReset" type="reset" value="">
            </form>
            <!---->
            <p class="state"> keyui
                
                
            </p>
            <h2 style="font-weight: 100; margin-top: 15px"></h2>
            <hr/>
            <div class="linkage">
                <p>:</p><br/>
                <span>/^[a-zA-Z][a-zA-Z0-9]{3,15}/</span><br/>
                <span>/^[0-9a-z@!$%&*#]{8,16}$/</span><br/>
                <span>/^1[3-9]\d{9}$/</span><br/>
                <span>/^([A-Za-z0-9_\-\.])+\@([A-Za-z0-9_\-\.])+\.([A-Za-z]{2,4})$/</span><br/>
                <span>/^[1-9]\d{5}(18|19|([23]\d))\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{3}[0-9Xx]$/</span>
            </div>
            <div class="linkage">
                <p>:</p>
                <iframe class="KeyUiNavCode" src="../../../static/three.txt">
                </iframe>
            </div>
        </div>

    </div>
</template>

<script>

    export default {
        data() {
            // el:".select"
            return {
                 flag: false,
                flag1: false,
                flag2: false,
                flag3: false,
                flag4: false,
                flag5: false,
                cityData: [
                    {
                        province: "",
                        citys: [
                            {city: "", areas: ["", "", "", ""]}
                        ]
                    },
                    {
                        province: "",
                        citys: [
                            {city: "", areas: ["", "", "", "", ""]}
                        ]
                    }
                ],
                user: {
                    selCity: {
                        sheng: "",
                        shi: "",
                        qu: ""
                    }

                }
            }
        },
        updated() {
            // layui.use('code', function () { //code
            //     layui.code(); //code
            // });
        },
        mounted() {
            layui.use('code', function(){ //code
                layui.code(); //code
            });
            // 
            $("#FormSubmit").click(() => {
                if (this.flag1 && this.flag2 && this.flag3 && this.flag4 && this.flag5) {
                    alert("")
                }
                else {
                    alert("");
                }
            })
            // 

            $("#box").click(function () {
                $("#uls").slideToggle("slow");
            })

            $("#uls li").click(function () {
                $("#uls li").css({"background": "#fff"});
                $(this).css({
                    background: "#009688",
                })

                let lue = $(this).html();
                $("#con").html(lue)
            })
        },
        methods: {

            ////////////////
            RegularUser() {
                let str = $(".FormUser").val();
                let patt1 = /^[a-zA-Z][a-zA-Z0-9]{3,15}$/;
                // let flag1=false;
                if (patt1.test(str) == true) {
                    this.flag1 = true;
                    $("#UserPrompt").html("").css({
                        color: "green"
                    });
                } else {
                    $("#UserPrompt").html("").css({
                        color: "red"
                    });
                }
            },

            ////////////////
            RegularPassWord() {
                let str = $(".FormPassword").val();
                let patt2 = /^[0-9a-z@!$%&*#]{8,16}$/;
                // var flag2=false;
                if (patt2.test(str) == true) {
                    this.flag2 = true;
                    $("#PassWordPrompt").html("").css({
                        color: "green"
                    });
                } else if (patt2.test(str) == false) {

                    $("#PassWordPrompt").html("").css({
                        color: "red"
                    });
                }
            },
            ///////////////
            RegularPhone() {
                let str = $(".FromPhone").val();
                let patt3 = /^1[3-9]\d{9}$/;
                // var flag3=false;
                if (patt3.test(str) == true) {
                    this.flag3 = true;
                    $("#PhonePrompt").html("").css({
                        color: "green"
                    });
                } else if (patt3.test(str) == false) {

                    $("#PhonePrompt").html("").css({
                        color: "red"
                    });
                }
            },
            //
            RegularEmail() {
                let str = $(".FromEmail").val();
                let patt4 = /^([A-Za-z0-9_\-\.])+\@([A-Za-z0-9_\-\.])+\.([A-Za-z]{2,4})$/;
                // var flag4=false;
                if (patt4.test(str) == true) {
                    this.flag4 = true;
                    $("#EmailPrompt").html("").css({
                        color: "green"
                    });
                } else if (patt4.test(str) == false) {

                    $("#EmailPrompt").html("").css({
                        color: "red"
                    });
                }
            },
            //
            RegularCard() {
                let str = $(".FromCard").val();
                // var flag5=false;
                let patt5 = /^[1-9]\d{5}(18|19|([23]\d))\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{3}[0-9Xx]$/;
                if (patt5.test(str) == true) {
                    this.flag5 = true;
                    $("#CardPrompt").html("").css({
                        color: "green"
                    });
                } else if (patt5.test(str) == false) {

                    $("#CardPrompt").html("").css({
                        color: "red"
                    });
                }
            },

        }
    }
</script>

<style lang="scss" scoped>
    * {
        padding: 0px;
        margin: 0px;
    }

    .BigBox {
        margin-top: 80px;
        margin-left: 50px;
        width:85%;
        h2 {
            padding: 10px 20px;
            background: #f2f2f2;
            border-left: 4px solid #009688;
            font-size: 16px;
            border-radius: 3px;
        }
    }

    .left {
        width: 86.955%;
        background: white;
        float: right;

        form {
            font-size: 14px;
            margin-top: 30px;
            label:nth-of-type(1) {
                margin-left: 15px;
            }
            label:nth-of-type(2) {
                margin-left: 30px;
            }
            input {
                margin-bottom: 15px;
                width: 250px;
                height: 37px;
            }
            .live {
                display: inline-block;
                vertical-align: middle;
                margin-left: 24px;
            }
            #box {
                vertical-align: middle;
                display: inline-block;
                width: 300px;
                height: 37px;
                border: 1px solid #e6e6e6;
                position: relative;
                background: white;
                #con {
                    width: 250px;
                    height: 37px;
                    line-height: 37px;
                    display: inline-block;
                }
                #ps01 {
                    padding-left: 10px;
                    line-height: 37px;

                }
                #uls {
                    /*display: none;*/
                    position: absolute;
                    left: 0px;
                    top: 37px;
                    /*box-shadow: 0 0 5px;*/
                    background: white;
                    height: 200px;
                    overflow-y: auto;
                    border: 1px solid #cccccc;
                    border-top: 0;
                    li {
                        line-height: 37px;
                        list-style: none;
                        width: 283px;
                        height: 37px;
                    }
                    li:nth-of-type(1) {
                        background: #009688;
                    }
                }

            }
            /**/
            .check {
                padding-top: 15px;
                overflow: hidden;
                label {
                    float: left;
                    margin-right: 10px;
                }
                input {
                    float: left;
                    width: 20px;
                    height: 20px;
                    margin-right: 10px;
                }
            }
            /**/
            .radio {
                /*padding-top: 15px;*/
                overflow: hidden;
                label {
                    margin-right: 10px;
                    float: left;
                    display: inline-block;
                    vertical-align: middle;

                }
                input {
                    margin-right: 10px;
                    width: 20px;
                    float: left;
                    height: 20px;
                }
            }
            /**/
            .address {
                select {
                    width: 80px;
                    height: 30px;
                }
            }

            /**/
            .text {
                margin-top: 8px;
                textarea {
                    margin-left: 100px;
                }
            }
            /**/
            input:nth-of-type(6) {
                width: 100px;
                border: 0;
                border-radius: 5px;
                background: #009688;
                color: white;
                margin-left: 100px;
            }
            input:nth-of-type(7) {
                border-radius: 5px;
                width: 100px;
                border: 0;
            }
        }
        /**/
        .state {
            padding: 10px 20px;
            background: #f2f2f2;
            border-left: 4px solid #009688;
            font-size: 16px;
            border-radius: 3px;

        }
        /**/
        .code {
            width: 90%;
            border-top: 30px;
            background: #f0f0f0;
            color: black;
            margin-top: 20px;
            border-bottom:1px solid #ccc;
            p {
                width:100%;
                padding-left:10px;
                background:#f2f2f2;
                height:30px;
                line-height:30px;
                color:#737383;
                font-size:16px;
                box-sizing: border-box;

            }
            span {
                line-height: 35px;
                margin-left: 30px;
            }
        }
        .linkage {
            margin-top: 30px;
            background: #f0f0f0;
            padding-left:20px;
            padding-bottom:30px;
            p{
                display:block;
                width:90%;
                padding-left:10px;
                background:#f2f2f2;
                height:30px;
                line-height:30px;
                color:#737383;
                font-size:16px;
                box-sizing: border-box;
                border-bottom:1px solid #ccc;
            }
        }
    }

    .KeyUiNavCode {
        box-sizing:border-box;
        width: 90%;
        height: 500px;
        background: #f0f0f0;
        border: 0;
        resize: none;
    }
</style>



