<template>
  <div class="evaStar">
    <ul class="star">
      <li v-for="(itemClass,index) in itemClasses" :class="itemClass" class="star-item" @click="stars(index)" track-by="index" :key='index'></li><!-- track-by -->
    </ul>
  </div>
</template>

<script>
    export default {
      name: "ScoreTwo",
      data(){
        return{
          score: 4,
        }
      },
      computed:{ //
        itemClasses(){
          let result = []; // ,
          let score = Math.floor(this.score * 2 ) / 2; // 
          let hasDecimal = score % 1 !== 0; // 
          let integer = Math.floor(score); // 
          for(let i=0;i<integer;i++){ // on
            result.push("on"); // pushCLS_ON
          }
          if(hasDecimal){ // half
            result.push("half"); // 

          }
          while(result.length < 5){// ,off
            result.push("off");
          }
          return result;
        }
      },
      methods:{
        stars:function(index){
          this.score = index + 1
        }
      }
    }
</script>

<style scoped>
  .evaStar{
    margin: 20px;
  }
  .star-item{
    display: inline-block;
    background-repeat: no-repeat;
    width: 30px;
    height: 30px;
    background-size: 100%;
  }
  .star-item.on{
    background-image: url(../../../static/img/shineon.png);
  }
  .star-item.off{
    background-image: url(../../../static/img/shineoff.png);
  }
</style>



